.App {
  text-align: center;
}

.container {
  max-width: 1080px;
  max-height: 768px;
  margin: auto;
}

.addTaskButton {
  background-color: #008CBA;
  border-radius: 4px;
  color: white;
  font-size: 16px;
  padding: 10px 20px;
}

.modal {
    display: none; 
    position: fixed; 
    z-index: 99; 
    padding-top: 100px; 
    left: 0;
    top: 0;
    width: 100%; 
    height: 100%; 
    overflow: auto; 
    background-color: rgb(0,0,0,0.3); 
    box-sizing: border-box;
}

/* Modal Content */
.modal-content {
    background-color: #fefefe;
    margin: auto;
    border: 1px solid #888;
    width: 30%;
    padding-bottom: 10px;
}

.modalHeader {
  font-size: 18px;
  background-color: #7ed321; 
  color: white;
  padding: 4px 8px;
  max-width: 100%;
  top: 0;
  /*border: 2px solid none*/
}

.modal-content ul {
  list-style: none;
  text-align: center;
  margin: 8px;
  padding: 0;
}

.modal-content li {
  display: block;
  margin-bottom: 10px;
}

.modal-content li label {
  margin-right: 5px;
  min-width: 20px;
  max-width: 40px;
  font-size: 16px;
}

.listContainer ul {
  list-style: none;
}

.buttonContainer {
  text-align: center;
}

.buttonClass {
  border-radius: 4px;
  color: white;
  font-size: 12px;
  padding: 8px 14px;
  margin-right: 8px
}

.primary {
  background-color: #3f51b5;
}

.secondary {
  background-color: #e43e31;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
